.statistics {
  text-align: center;
  background-color: #fff;
  width: 400px;
  margin: 0 auto;
  box-shadow: rgb(0 0 0 / 25%) 0px 54px 55px, rgb(0 0 0 / 12%) 0px -12px 30px,
    rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px,
    rgb(0 0 0 / 9%) 0px -3px 5px;
  border-radius: 15px;
  overflow: hidden;
}

.title {
  padding: 30px 0;
  margin: 0;
  font-weight: 900;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 25px;
}

.statList {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.item {
  padding: 15px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.label {
  color: #fff;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.percentage {
  color: #fff;
  padding-top: 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 900;
}
